:root {
  --font-josefin: "Josefin Sans", sans-serif;
  --font-playfair: "Playfair Display", serif;
}

:root {
  --white: hsl(0, 0%, 100%);
  --black: hsl(0, 0%, 0%);
  --overlay: rgba(0, 0, 0, 0.5);
  --light-grey: #F2F2F2;
  --dark-grey: #222;
}

html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  font-family: var(--font-josefin);
}

ul {
  list-style: none;
}

img {
  max-width: 100%;
  display: block;
}

h1,
h2,
h3,
h4,
h5 {
  margin-top: 0;
  line-height: 1.1;
  font-weight: normal;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  padding: 0 1.5rem;
  max-width: 60rem;
  margin: 0 auto;
}
@media (min-width: 60em) {
  .container {
    padding: 0;
    transform: translate(0, 0);
  }
}

.header {
  display: grid;
  justify-content: end;
  padding-block: 1.5rem;
}
.header .menu {
  position: absolute;
  top: 65px;
  width: calc(100% - 48px);
  background: var(--white);
  padding: 0.625rem;
  margin: 0 auto;
  left: 50%;
  transform: translateX(-50%);
  opacity: 0;
  visibility: hidden;
}
.header .menu__links {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  gap: 0.3125rem 1rem;
  border: thick double var(--light-grey);
  padding: 1rem;
  text-align: center;
}
.header .menu__links a {
  padding-block: 0.3125rem;
  display: block;
  color: var(--dark-grey);
}
.header .menu__links a:hover {
  box-shadow: 0 2px 0 var(--light-grey);
}
@media (min-width: 47.5em) {
  .header .menu__links a {
    color: var(--white);
  }
}
@media (min-width: 47.5em) {
  .header .menu__links {
    display: flex;
    border: none;
    padding: 0;
  }
}
@media (min-width: 47.5em) {
  .header .menu {
    position: relative;
    visibility: visible;
    opacity: 1;
    top: 0;
    right: 0;
    width: 100%;
    padding: 0;
    background: none;
  }
}
@media (min-width: 47.5em) {
  .header {
    place-content: center;
  }
}

.hero {
  background-image: linear-gradient(rgba(0, 0, 0, 0.5), rgba(0, 0, 0, 0.5)), url(../../../img/hero-bg.jpeg);
  background-size: cover;
  background-position: center;
  height: 100vh;
}
.hero__info {
  padding-top: 0.625rem;
  text-align: center;
  color: var(--white);
}
.hero__info .logo {
  margin-bottom: 3rem;
}
.hero__info .logo img {
  margin: 0 auto;
}
.hero__info h1 {
  font-size: 2.125rem;
  font-family: var(--font-playfair);
  margin-bottom: 1.25rem;
}
.hero__info p {
  line-height: 1.5625rem;
  max-width: 43.75rem;
  margin: 0 auto 2.5rem auto;
}
.hero__info .arrow {
  font-size: 1.5rem;
  animation: arrowSlide 1s alternate-reverse infinite;
  margin-top: 30px;
}

.menu-mobile {
  cursor: pointer;
  display: grid;
  place-content: center;
  width: 1.5rem;
  height: 1.5rem;
}
.menu-mobile .menu__icon {
  width: 24px;
  height: 4px;
  background-color: var(--white);
  border-radius: 6px;
  transition: all 0.4s ease-in-out;
}
.menu-mobile .menu__icon::before, .menu-mobile .menu__icon::after {
  content: "";
  position: absolute;
  width: 24px;
  height: 4px;
  background: var(--white);
  border-radius: 6px;
  transition: all 0.4s ease-in-out;
}
.menu-mobile .menu__icon::before {
  transform: translateY(-9px);
}
.menu-mobile .menu__icon::after {
  transform: translateY(9px);
}
@media (min-width: 47.5em) {
  .menu-mobile {
    display: none;
  }
}

.menu-mobile.open .menu__icon {
  transform: translateX(-50px);
  background: transparent;
}

.menu-mobile.open .menu__icon::before {
  transform: rotate(45deg) translate(35px, -35px);
}

.menu-mobile.open .menu__icon::after {
  transform: rotate(-45deg) translate(35px, 35px);
}

.hide {
  display: none;
}

@keyframes arrowSlide {
  from {
    transform: translateY(-20px);
  }
  to {
    transform: translateY(0);
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
    visibility: hidden;
  }
  0% {
    opacity: 0;
    visibility: visible;
  }
  to {
    opacity: 1;
    visibility: visible;
  }
}
.fade-in {
  animation: fadeIn 200ms ease-in forwards;
}

@keyframes fadeOut {
  from {
    opacity: 1;
    visibility: visible;
  }
  100% {
    opacity: 0;
    visibility: visible;
  }
  to {
    opacity: 0;
    visibility: hidden;
  }
}
.fade-out {
  animation: fadeOut 200ms ease-in forwards;
}

.cardapio {
  padding-block: 3.75rem;
}
.cardapio__titulo {
  text-align: center;
  margin-bottom: 3.75rem;
  grid-column: 1/-1;
}
.cardapio__titulo h4 {
  font-size: 1.125rem;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  font-weight: 300;
  margin-bottom: 1.25rem;
}
.cardapio__titulo h1 {
  font-size: 2.25rem;
  font-family: var(--font-playfair);
}
.cardapio__info {
  text-align: center;
  margin-bottom: 2.5rem;
}
.cardapio__info h5 {
  font-size: 1rem;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
  font-weight: 300;
  margin-bottom: 1.25rem;
}
@media (min-width: 60em) {
  .cardapio {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
  }
}

.snacks {
  background: url(../../../img/cardapio-bg.jpg) no-repeat;
  background-size: cover;
  background-position: 36.88% 50%;
  padding: 3.75rem 1.5rem;
}
.snacks .grid {
  display: grid;
  grid-template-columns: repeat(2, minmax(140px, 175px));
  place-content: end;
  gap: 1.5rem;
}
@media (min-width: 47.5em) {
  .snacks .grid {
    grid-template-columns: repeat(3, minmax(140px, 175px));
  }
}
.snacks__info {
  background-color: var(--overlay);
  color: var(--white);
  padding: 1.875rem 1.25rem;
  font-size: 1rem;
  text-transform: uppercase;
  text-align: center;
  display: grid;
  gap: 1.25rem;
}
@media (min-width: 60em) {
  .snacks__info {
    padding-top: 3.4375rem;
  }
}
.snacks__icon {
  width: 3rem;
  height: 3rem;
  margin: 0 auto;
}

.sobre {
  padding-block: 3.75rem;
}
.sobre__info {
  text-align: center;
  padding: 1.875rem;
  display: grid;
  gap: 1.25rem;
}
.sobre__info h5 {
  font-size: 1.125rem;
  text-transform: uppercase;
  letter-spacing: 0.25rem;
}
.sobre__info h4 {
  font-size: 1.5rem;
  font-family: var(--font-playfair);
}
.sobre__info a {
  font-size: 0.875rem;
  color: var(--dark-grey);
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  display: block;
  max-width: max-content;
  margin: 0 auto;
  padding-block: 0.3125rem;
  box-shadow: 0 2px var(--dark-grey);
}
.sobre .img-bolo {
  background: url(../../../img/blog-foto1.jpg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 363px;
}
.sobre .img-frutas {
  background: url(../../../img/blog-foto2.jpeg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 363px;
}
.sobre .img-salada {
  background: url(../../../img/blog-foto3.jpeg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 363px;
}
@media (min-width: 47.5em) {
  .sobre {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}

.health {
  background: var(--light-grey);
  padding-block: 1.875rem;
}
.health__info {
  padding: 1.875rem 2.5rem;
}
.health__info h2 {
  font-size: 2.25rem;
  font-family: var(--font-playfair);
  margin-bottom: 1.25rem;
}
.health__info p {
  margin-bottom: 1.25rem;
  line-height: 1.5625rem;
}
.health__info a {
  font-size: 0.875rem;
  color: var(--dark-grey);
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  padding-block: 0.3125rem;
  box-shadow: 0 2px var(--dark-grey);
}
.health__img {
  background: url(../../../img/saudavel-img.jpeg) no-repeat;
  background-position: center;
  background-size: cover;
  height: 322px;
}
@media (min-width: 47.5em) {
  .health__img {
    height: 100%;
  }
}
@media (min-width: 47.5em) {
  .health .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-content: center;
  }
}

.reviews {
  background: var(--light-grey);
  padding-block: 3.75rem;
}
.reviews__info {
  padding: 1.875rem 2.5rem;
  display: grid;
  gap: 1.25rem;
}
.reviews__info h4 {
  font-size: 1.125rem;
  letter-spacing: 0.25rem;
  text-transform: uppercase;
}
.reviews__info h2 {
  font-size: 2.25rem;
  font-family: var(--font-playfair);
  text-transform: capitalize;
}
.reviews__info .aspas {
  width: 2.0625rem;
}
.reviews__info p {
  line-height: 1.5625rem;
}
.reviews__info--autor {
  letter-spacing: 0.25rem;
  text-transform: uppercase;
  margin-bottom: 1.25rem;
}
.reviews__img {
  background: url(../../../img/reviews-img.jpeg) no-repeat;
  background-size: cover;
  background-position: center;
  height: 349px;
}
@media (min-width: 47.5em) {
  .reviews .grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    place-content: center;
  }
}

.aulas {
  padding-block: 3.75rem;
  text-align: center;
}
.aulas__info h2 {
  font-size: 2.25rem;
  font-family: var(--font-playfair);
  margin-bottom: 1.25rem;
}
.aulas__info p {
  line-height: 1.5625rem;
  margin-bottom: 1.25rem;
}
.aulas__img {
  display: grid;
  gap: 0.625rem;
  margin-bottom: 1.875rem;
}
.aulas__img--1 {
  height: 323px;
  background: url(../../../img/slide-1.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.aulas__img--2 {
  height: 323px;
  background: url(../../../img/slide-2.jpg) no-repeat;
  background-size: cover;
  background-position: center;
}
.aulas__img--3 {
  height: 323px;
  background: url(../../../img/slide-3.jpeg) no-repeat;
  background-size: cover;
  background-position: center;
}
@media (min-width: 47.5em) {
  .aulas__img {
    grid-template-columns: repeat(3, 1fr);
  }
}
.aulas a {
  font-size: 0.875rem;
  color: var(--dark-grey);
  text-transform: uppercase;
  letter-spacing: 0.125rem;
  display: inline-block;
  padding-block: 0.3125rem;
  box-shadow: 0 2px var(--dark-grey);
}

.quote {
  background: var(--light-grey);
  text-align: center;
  padding-bottom: 3.75rem;
}
.quote__img {
  margin-bottom: 3.75rem;
}
.quote__img--1 {
  height: 563px;
  background: url(../../../img/quote-img1.jpeg);
  background-size: cover;
  background-position: center;
}
.quote__img--2 {
  height: 563px;
  background: url(../../../img/quote-img2.jpeg);
  background-size: cover;
  background-position: center;
}
.quote__img--3 {
  height: 563px;
  background: url(../../../img/quote-im3.jpeg);
  background-size: cover;
  background-position: center;
}
@media (min-width: 47.5em) {
  .quote__img {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
  }
}
.quote blockquote {
  max-width: 800px;
  margin: 0 auto 1.25rem;
}
.quote blockquote p {
  line-height: 2rem;
  font-style: italic;
  font-size: 1.25rem;
}
.quote ul {
  font-size: 1.25rem;
  display: flex;
  justify-content: center;
}
.quote ul a {
  padding: 0.3125rem;
  display: inline-block;
  margin-inline: 0.3125rem;
  color: var(--dark-grey);
}

.footer {
  background: var(--dark-grey);
  color: var(--light-grey);
  padding-block: 1.25rem;
  text-align: center;
}
.footer a {
  color: var(--white);
  text-decoration: underline;
}